<template>
  <div id="app">
    <router-view />
    <!-- Modal -->
    <TweetCreate />
    <ReplyCreate />
    <!-- Modal -->
  </div>
</template>

<script>
import TweetCreate from "../src/components/TweetCreate.vue";
import ReplyCreate from "../src/components/ReplyCreate.vue";

export default {
  components: {
    // 不同元件內各自皆有啟動「新增推文」「新增回覆」的按鈕
    // 將元件統一綁定於 App.vue，避免跨元件呼叫的路徑過於繁瑣
    TweetCreate,
    ReplyCreate
  },
  methods: {
    // 新增推文 modal
    showNewCreateModal() {
      this.$modal.show("tweetCreate");
    },
    hideNewCreateModal() {
      this.$modal.hide("tweetCreate");
    },
    // 新增回覆 modal
    showReplyModal() {
      this.$modal.show("replyCreate");
    },
    hideReplyModal() {
      this.$modal.hide("replyCreate");
    },
  },
};
</script>

<style lang="scss">
@import "/src/scss/App.scss";
</style>