import Vue from 'vue'
import store from './../store'
import VueRouter from 'vue-router'
import SignIn from '../views/SignIn.vue'
import Main from '../views/Main.vue'
import NotFound from '../views/NotFound.vue'

Vue.use(VueRouter)

// 用於路由 beforeEnter功能，當使用者在路由中跳轉網址時，檢查使用者身分 (user/admin)
const authorizeIsAdmin = (to, from, next) => {
  // 檢查是否為「管理員」，避免使用者進入後台畫面
  const currentUser = store.state.currentUser
  if (currentUser && currentUser.role === "user") {
    // 若發現身分為「使用者」，轉移至前台登入畫面
    return next('/signin')
  }
  next()
}

const authorizeIsUser = (to, from, next) => {
  // 檢查是否為「使用者」，避免管理員進入前台畫面
  const currentUser = store.state.currentUser
  if (currentUser && currentUser.role === "admin") {
    // 若發現身分為「管理員」，轉移至後台登入畫面
    return next('/admin/signin')
  }
  next()
}

const routes = [
  {
    path: '/',
    name: 'root',
    redirect: '/signin'
  },
  {
    path: '/signin',
    name: 'sign-in',
    component: SignIn
  },
  {
    path: '/signup',
    name: 'sign-up',
    component: () => import('../views/SignUp.vue')
  },

  {
    path: '/users',
    name: 'users',
    component: () => import('../views/Users.vue'),
    redirect: { name: "main" },
    beforeEnter: authorizeIsUser,
    children: [
      {
        path: 'main',
        name: 'main',
        component: Main,
      },
      {
        path: 'tweet/:id',
        name: 'tweet',
        component: () => import('../views/Tweet.vue'),
      },
      {
        path: "self",
        name: "self",
        component: () => import('../views/UserSelf.vue'),
        redirect: { name: "selfTweet" },
        children: [
          {
            path: "tweet",
            name: "selfTweet",
            component: () => import('../components/TweetCardSelf.vue'),
          },
          {
            path: "reply",
            name: "reply",
            component: () => import('../components/ReplyCardSelf.vue'),
          },
          {
            path: "like",
            name: "like",
            component: () => import('../components/LikeCardSelf.vue'),
          },
        ]
      },
      {
        path: ":id",
        name: "other",
        component: () => import('../views/UserOther.vue'),
        redirect: { name: "otherTweet" },
        children: [
          {
            path: "tweet",
            name: "otherTweet",
            component: () => import('../components/TweetCardSelf.vue'),
          },
          {
            path: "reply",
            name: "otherReply",
            component: () => import('../components/ReplyCardSelf.vue'),
          },
          {
            path: "like",
            name: "otherLike",
            component: () => import('../components/LikeCardSelf.vue'),
          },
          {
            path: 'followers',
            name: 'followers',
            component: () => import('../components/FollowerCards.vue')
          },
          {
            path: 'followings',
            name: 'followings',
            component: () => import('../components/FollowingCards.vue')
          }
        ]
      }
    ]
  },


  {
    path: '/admin/signin',
    name: 'admin-signin',
    component: () => import('../views/AdminSignIn.vue'),
  },
  {
    path: '/admin/tweets',
    name: 'admin-tweets',
    component: () => import('../views/AdminTweets.vue'),
    beforeEnter: authorizeIsAdmin
  },
  {
    path: '/admin/users',
    name: 'admin-users',
    component: () => import('../views/AdminUsers.vue'),
    beforeEnter: authorizeIsAdmin
  },
  {
    path: '/setting',
    name: 'setting',
    component: () => import('../views/Setting.vue'),
    beforeEnter: authorizeIsUser
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFound
  },
]

const router = new VueRouter({
  routes
})

router.beforeEach(async (to, from, next) => {

  // 從 localStorage 和 vuex 取出 token
  const tokenInLocalStorage = localStorage.getItem('token')
  const tokenInStore = store.state.token

  // 預設是 vuex 內的驗證狀態
  let isAuthenticated = store.state.isAuthenticated

  // 當網頁「重新整理」，vuex store會被清空，此時 tokenInLocalStorage !== tokenInStore，需再次像伺服器進行身分驗證
  if (tokenInLocalStorage && tokenInLocalStorage !== tokenInStore) {
    // 取得驗證成功與否
    isAuthenticated = await store.dispatch('fetchCurrentUser')
  }

  // 對於不需要驗證 token 的頁面
  const pathsWithoutAuthentication = ['sign-up', 'sign-in', 'admin-signin']

  // 如果 token 無效且進入需要驗證的頁面則轉址到登入頁
  if (
    !isAuthenticated &&
    !pathsWithoutAuthentication.includes(to.name)
  ) return next('/signin')

  // 如果 token 有效且進入不需要驗證到頁面則轉址到首頁
  if (
    isAuthenticated &&
    store.state.currentUser.role === "user" &&
    pathsWithoutAuthentication.includes(to.name)
  ) return next('/users/main')

  if (
    isAuthenticated &&
    store.state.currentUser.role === "admin" &&
    pathsWithoutAuthentication.includes(to.name)
  ) return next('/admin/tweets')

  next()
})

export default router
